.cont-i-cart{
    border-radius: 10%;
    position: relative;
    height: 30vh;
    width: 100%;
}

.img-i-carrito{
    position: absolute;
    width: 100%;
    height: 100%;
}

.subitems-i-cart{
    height: 6vh;
    width: 100%;
    background: rgba(65, 62, 62, 0.5);
}

.title-i-carrito{
    color: #fff;
    font-weight: 300;
    font-size: 2vh;
}

.count-i-carrito{
    margin: 0;
    color: #fff;
    font-weight: 700;
    font-size: 4vh;
}

.desc-i-carrito{
    text-shadow: 0.3vh 0.3vh #000;
    margin: 0 0 0 1vh;
    color: #fff;
    font-weight: 300;
    font-size: 3vh;
}
.price-i-carrito{
    text-shadow: 0.3vh 0.3vh #000;
    margin: 0 0 0 1vh;
    color: #fff;
    font-weight: 700;
    font-size: 4vh;
}