.main-container-av{
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
}

.main-title-av{
    width: 100%;
    text-align: center;
    font-weight: 100;
    font-size: 25;
    margin: 17px 0px 20px 0px;
}

.sub-label-av{
    font-weight: 100;
    font-size: 20px;
}

.mini-label-av{
    font-weight: 100;
    font-size: 10px;
    text-align: center;
}

.row-av{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.first-video{
    margin-right: 15px;
    width: 150px;
}

.second-video{
    width: 150px;
}

.inputfile-av{
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile-av + label {
    width: 150px;
    font-size: 1em;
    text-align: center;
    font-weight: 200;
    border: 1px solid #51DCEC;
    color: #51DCEC;
    background-color:  #fff;
    display: inline-block;
}

.inputfile-av:focus + label,
.inputfile-av + label:hover {
    background-color:  #51DCEC;
    color: #fff;
}

.btn-av{
    width: 150px;
    height: 30px;
    border-radius: 18px;
    border: 1px solid #51DCEC;
    color: #51DCEC;
    background-color: #fff;
    text-decoration: none;
    box-shadow: none;
    margin-top: 10px;
    margin-bottom: 15px;
}

.txtares-av{
    border-radius: 5%;
    border: 1px solid rgb(197, 197, 197);
    color: #51DCEC;
}

.msg-up{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

