/*Color de fondo*/
.black{
    background-color: #000;
}

.title-store-totem{
    height: 15vh;
    width: 100%;
}

/*Imagenes estáticas*/
.wh-container-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-position: 50% 50%;
}

/*Rellenar todo el div*/
.wh-full-img{
    height: 100%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center center;
    background-position: 50% 50%;
}

.title-mct{
    font-size: 30px;
    font-weight: 200;
    color: #fff;
    margin-top: 42px;
    margin-bottom: 20px;
}

.catalog-label { 
    border-top-left-radius: 8%;
    border-top-right-radius: 8%;
    color: #fff; 
    font-size: 15px; 
    padding: 7px 10px;
    margin: 0 !important;
    box-shadow: inset -8px -8px 18px 1px rgba(36, 36, 36, 0.75);
}

.catalog-label:nth-child(6n+1) { background: darkslategrey; }
.catalog-label:nth-child(6n+2) { background: rgb(105, 148, 62); }
.catalog-label:nth-child(6n+3) { background: rgb(173, 175, 54); }
.catalog-label:nth-child(6n+4) { background: rgb(112, 96, 255); }
.catalog-label:nth-child(6n+5) { background: rgb(64, 165, 196); }
.catalog-label:nth-child(6n+6) { background: rgb(172, 132, 47); }

.nopadding {
    padding: 0 !important;
    margin: 0 !important;
 }