.main-formaddimg{
    flex: 1;
}
.main-container-ai{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 20px 15px 0px 15px;
}

.form-ai{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.container-rows-ai{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 5px;
}
  
.inputfile-ai{
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile-ai + label {
    width: 200px;
    font-size: 1em;
    text-align: center;
    font-weight: 200;
    border: 1px solid #51DCEC;
    color: #51DCEC;
    background-color:  #fff;
    display: inline-block;
}

.inputfile-ai:focus + label,
.inputfile-ai + label:hover {
    background-color:  #51DCEC;
    color: #fff;
}
  
.btn-upload{
    width: auto;
    height: 30px;
    border-radius: 18px;
    border: 1px solid #51DCEC;
    color: #51DCEC;
    background-color: #fff;
    text-decoration: none;
    box-shadow: none;
    margin-top: 10px;
    margin-bottom: 15px;
}
  
textarea{
    margin: 20px auto;
    width: 200px;
}

.img-ai{
    width:60px;
    height:60px;
    margin-left:20px;
    border: 1px solid #51DCEC;
}

.list-imgs-products-ai{
    width: 100%;
    display: flex;
    flex-flow: wrap;
}

.row-imgs-ai{
    margin: 0px 10px 0px 10px;
}

.layer-toast{
      width: 100%;
      height: 100%;
}

.msg-ai{
  width: 100%;
  height: 40px;
}

.title-list-stores{
    margin: 15px 0px 10px 0px;
    font-weight: normal;
    font-size: 18px;
}
