.img-catalog-totem{
    height: 30vh;
}

.layer-relative{
    position: relative;
}

.layer-img-ict{
    width: 100%;
    height: 100%;
    z-index: -0.1;
    position: absolute;
}

.layer-label-ict{
    z-index: -0.09;
    position: absolute;
    background: rgb(0, 0, 0,0.7);
    width: 70%;
}

.tag-product-ict{
    color: #fff;
    font-size: small;
    font-weight: 700;
    margin-left: 5px;
    margin-bottom: 0;
    z-index: 1;
}
.sub-tag-product-ict{
    color: #fff;
    font-size: small;
    font-weight: 500;
    margin-left: 5px;
    margin-bottom: 0;
    z-index: 1;
}