
/*Imagenes*/
.full-screen {
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
}

@media (min-width: 650px ) {
    .full-screen {
        background-position: center center;
    }
}

/*Transición de videos*/
.hidden {
    position: absolute;
    opacity: 0.00001;
}