
.main_form_p{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Titulo de pantlla */
.txt_title_p{
    text-align: center;
    font-size: 3vh;
    font-weight: 300;
    color: #fff;
    margin: 0;
}

.txt_total_p{
    text-align: right;
    color: #fff;
    font-weight: 400;
    font-size: 2vh;
    margin: 0 2vh 0 0;
    padding: 0;
}

.txt_medium_p{
    color: #fff;
    font-weight: 400;
    font-size: 4vh;
    margin: 0;
    padding: 0;
}

.txt_info_p{
    color: #fff;
    font-weight: 100;
    font-size: 1.5vh;
    text-align: center;
    margin: 2px;
    padding: 0;
}

.txt_subtitle_p{
    text-align: center;
    color: #fff;
    font-weight: 400;
    font-size: 2vh;
    margin: 0;
    padding: 0;
}

.field_p::placeholder{
    text-align: center;
    color: #51DCEC;
}

.field_p{
    border: 1px solid  #51DCEC;
    text-align: center;
    margin: 0 5px;
}

input:focus{
    outline: 0;
}

.btn_p{
    width: 150px;
    height: 40px;
    border-radius: 1%;
    color: #000;
    background-color: #51DCEC;
    opacity: 0.8;
    text-decoration: none;
    box-shadow: none;
    margin-top: 15px;
    margin-bottom: 25px;
    border-top: transparent !important;
    border-left: transparent !important;
    border-right: 1px solid #707070;
    border-bottom: 2px solid #707070;
}
